.avatar-img{
    height: 250px;
}
.description{
    background: black;
    opacity: 0.8;
    margin: auto;
    margin-top: 40px; 
    padding: 10px;
    width: 75%;
    border-radius: 10px;
}
.description h1{
    font-size: 60px;
    font-weight: bold;
}
.description hr{
    border: 2px solid white;
    width: 50%;
    margin: auto;
}
.description p{
    font-size: 20px;
   padding: 5px;
}
.landing{
    background: #050505;  /* fallback for old browsers */
    background: linear-gradient(to right, #29323c, #050505); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;    
    text-align:center;
    height: 80vh;
    width:100%;
    margin: auto;
  }
.content{
    background: #050505;  /* fallback for old browsers */
    background: linear-gradient(to right, #29323c, #050505); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;    
    height: 80vh;
    width:100%;
    margin: auto;
}
.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
}
.container.social-links{
    margin-top: -20px;
    margin-bottom: -50px;
}
@media(max-width:991px){
    .description h1 {
        font-size: 30px;
        font-weight: bold;
    }
    .description p{
        font-size: 15px;
       padding-top: 15px;
       font-weight: bold;
    }
}
@media(max-width:434px){
    .landing{
        height: 100vh;
    }
    .content{
        height: 100vh;
    }
    .description h1 {
        font-size: 15px;
        font-weight: bold;
    }
    .description p{
        font-size: 10px;
       padding-top: 15px;
       font-weight: bold;
    }
}